.backdrop {
    position: fixed;
    z-index: 2000;
    top: 0px;
    left: 0px ;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
    &.is-hidden {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        
        
    }
}